import React from 'react';
import { graphql } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';

import Layout from '../../components/Layout';
import Banner from '../../components/Banner';
import Container from '../../components/Container';
import Footer from '../../components/Footer';

import BlockHeaderNoImage from '../../components/BlockHeaderNoImage';
import BlockCaseStudyCards from '../../components/BlockCaseStudyCards';

export const pageQuery = graphql`
  {
    biggerBuyersImage: file(name: { eq: "bigger-buyers" }) {
      childImageSharp {
        fluid(maxWidth: 520) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    hyperikonLedTubes: contentfulAsset(title: { eq: "hyperikon-led-tubes" }) {
      title
      fluid {
        ...GatsbyContentfulFluid
      }
    }
    viceGolfBrandImage: contentfulAsset(
      title: { eq: "vice-golf-brand-image" }
    ) {
      title
      fluid {
        ...GatsbyContentfulFluid
      }
    }
  }
`;
export default function caseStudiesPage({ data }) {
  return (
    <Layout>
      <GatsbySeo title="Ð¡ase Studies" />
      <Banner />
      <Container>
        <BlockHeaderNoImage
          pretitle="CASE STUDIES"
          title="Magic in the details"
          desc={
            <>
              Our merchants have simplified their accounts receivable processes
              and grown sales by partnering with Resolve.
            </>
          }
        />

        <BlockCaseStudyCards
          items={[
            {
              title: 'Hyperikon',
              description:
                'How Hyperikon multiplied their b2b sales and reduced their deal closing time with Net Terms automation.',
              link: '/case-studies/hyperikon',
              image: data.hyperikonLedTubes.fluid,
            },
            {
              title: 'Vice Golf',
              description:
                'How Hyperikon multiplied their b2b sales and reduced their deal closing time with Net Terms automation.',
              link: '/case-studies/vice-golf',
              image: data.viceGolfBrandImage.fluid,
            },
          ]}
          showCTA={false}
        />
      </Container>
      <Footer />
    </Layout>
  );
}
